import TitleWidget from "./TitleWidget";
import RowWidget from "./RowWidget";
import TextWidget from "./TextWidget";
import TextAreaWidget from "./TextAreaWidget";
import ImageWidget from "./ImageWidget";
import AlertWidget from "./alertWidget";
import ButtonWidget from "./ButtonWidget";
import LinkWidget from "./LinkWidget";
import QuoteWidget from "./QuoteWidget";

// import { gsap } from "gsap";
import draggable from "vuedraggable";

// FIREBASE
const firebase = require("firebase");
require("firebase/firestore");
import { db } from '~/plugins/firebase/firebaseConfig.js'

export default {
  props: ["preview", "contenteditable", "layout", "rowIndex", "colIndex"],
  data() {
    return {
      showButton: true,
      showContentPanel: false,
      title: { type: "title", content: "<h1>Titre</h1>", i: 0 },
      text: { type: "text", content: "Paragraphe ", i: 0 },
      themeColors: "OK",
      image: {
        type: "image",
        content: "Image",
        i: 0,
        height: 100,
        width: 100,
        alt: "",
      },
      quote: {
        i: 0,
        type: "quote",
        content: "Citation...",
      },
      button: {
        i: 0,
        type: "button",
        content: "Cliquez ici",
        location: "",
        target: "",
      },
      link: { i: 0, type: "link", content: "Lien" },
      alert: {
        i: 0,
        type: "alert",
        content: "Alert",
        color: [{ label: "Succès", value: "success" }],
        icon: "",
      },
    };
  },
  created() {
    let globalThis = this;
    var getCustomers = db
      .collection("customers")
      .doc(localStorage.getItem("idCustomer"))
      .collection("sites")
      .doc(localStorage.getItem("idSite"))
      .get()
      .then(function(doc) {
        globalThis.themeColors = doc.data().themeColors;
      })
      .catch(function(error) {
        console.error("Error getting cached document:", error);
      });
  },
  methods: {
    row(arrayCol) {
      var colRender = [];
      for (let i = 0; i < arrayCol.length; i++) {
        colRender.push({
          content: [],
        });
      }
      return {
        type: "row",
        content: "",
        row: arrayCol,
        col: colRender,
        hover: false,
      };
    },
    handleDrop(e) {
      this.$emit(
        "updateLayout",
        this.rowIndex,
        this.colIndex,
        e.newIndex,
        e.oldIndex
      );
    },
    addItem(type) {
      this.layout[this.rowIndex].col[this.colIndex].content.push(
        JSON.parse(JSON.stringify(type))
      );
    },
  },
  computed: {
    dragOptions() {
      return {
        animation: 200,
        group: "description",
        disabled: this.preview ? true : false,
        ghostClass: "ghost",
      };
    },
  },
  components: {
    RowWidget,
    TextWidget,
    TextAreaWidget,
    ImageWidget,
    AlertWidget,
    ButtonWidget,
    LinkWidget,
    TitleWidget,
    QuoteWidget,
    draggable,
  },
};
